@import url(https://fonts.googleapis.com/css2?family=Secular+One&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Secular One', sans-serif;
  position: relative;
}
.Game{
  min-height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.title{
  font-size: 3rem;
}
.square{
    height: 48px;
    width: 48px;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.board-main{
   
}
.row{
    display: flex;
}
.status{
    font-size: 1.25em;
}
.board{
    position: relative;
}
.control{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
}
.control button{
    min-height: 25px;
    text-transform: capitalize;
    font-size: large;
    padding: 5px;
    cursor: pointer;
    border: 1px solid black;
}
.control button:focus{
    outline: none;
}

